<template>
    <div class="login">
        <b-container fluid class="h-100 mh-10">
            <b-row align-h="end" class="h-100 mh-100 login-row">
                <b-col cols="auto" class="login-block h-100 mh-100">
                    <b-row align-h="center" class="h-100">
                        <b-col cols="auto" class="w-100 text-center">
                            <h1>
                                <router-link
                                    :to="{
                                        name: 'index',
                                        params: {
                                            SYSTEM: $route.params.SYSTEM,
                                        },
                                    }"
                                    >Welcome</router-link
                                >
                            </h1>

                            <b-row align-h="center">
                                <b-col cols="auto">
                                    <label
                                        ><b-icon
                                            icon="person-fill"
                                            scale="2"
                                        ></b-icon
                                    ></label>
                                </b-col>
                                <b-col cols="auto">
                                    <b-form-input
                                        type="text"
                                        v-model="user.account"
                                        placeholder="Account"
                                        @keypress.enter.prevent
                                    ></b-form-input>
                                </b-col>
                            </b-row>
                            <b-row align-h="center">
                                <b-col cols="auto">
                                    <label for="input-default"
                                        ><b-icon
                                            icon="lock-fill"
                                            scale="2"
                                        ></b-icon
                                    ></label>
                                </b-col>
                                <b-col cols="auto">
                                    <b-form-input
                                        :type="pwd_type"
                                        v-model="user.password"
                                        placeholder="Password"
                                        @keypress.enter.prevent
                                    ></b-form-input>
                                    <b-icon
                                        v-if="!pwd_show"
                                        icon="eye-fill"
                                        class="pwdeye"
                                        @click="_showHidePwd(true)"
                                    ></b-icon>
                                    <b-icon
                                        v-else
                                        icon="eye-slash-fill"
                                        class="pwdeye"
                                        @click="_showHidePwd(true)"
                                    ></b-icon>
                                </b-col>
                            </b-row>
                            <p class="mt-3 forget">忘記密碼?</p>
                            <b-button
                                type="submit"
                                variant="primary"
                                size="lg"
                                @click.prevent="_checkUser"
                                >Sign In</b-button
                            >
                        </b-col>
                    </b-row>
                </b-col>
            </b-row>
        </b-container>
        <alertModal />
    </div>
</template>

<script>
import alertModal from "@/components/alertModal.vue";
import { mapGetters, mapActions } from "vuex";
import { childRouter } from "@/router/childRouter";
export default {
    name: "login",
    data() {
        return {
            user: {
                account: "",
                password: "",
                name: "",
                access_list: {},
                customerId:""
            },
            pwd_type: "password",
            pwd_show: false,
            SYSTEM: "CHUNZU",
        };
    },
    components: {
        alertModal,
    },
    computed: {
        ...mapGetters({
            axiosResult: "commonaxios/get_axiosResult",
        }),
    },
    created: function () {
        console.log(childRouter);
        console.log(this.$router);
    },
    methods: {
        ...mapActions({
            axiosGetAction: "commonaxios/axiosGetAction",
            setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
            toggleSetTimeOutAlertModal:
                "alertmodal/toggle_settimeout_alertModal",
            toggleAlertModal: "alertmodal/toggle_alertModal",
            changeLoginData: "getlogin/change_loginData",
            changePageAccess: "getlogin/change_pageAccess",
            changeIsLoginMoment: "getlogin/change_isLoginMoment",
            changeIsRoot: "getlogin/change_isRoot",
            setApiMap: "apidetail/set_apiMap",
        }),

        _checkUser(e) {
            var vm = this;
            if (vm.user.account === "") {
                vm.setSetTimeOutAlertMsg("帳號尚未輸入");
                vm.toggleSetTimeOutAlertModal();
                return;
            }

            vm.toggleAlertModal(true);
            const getApiMap = async () => {
                await vm._login(); //登入檢查
            };

            getApiMap()
                .then(() => {
                    let params = {};
                    params["url"] = "/Dashboard/ApiMap/" + vm.SYSTEM;
                    console.log(params);

                    vm.axiosGetAction(params).then(() => {
                        vm.toggleAlertModal(false);
                        var result = vm.axiosResult;
                        console.log(result);
                        console.log(typeof result);
                        if (result.status != 200) {
                            vm.setSetTimeOutAlertMsg(result.data);
                            vm.toggleSetTimeOutAlertModal();
                            return;
                        }
                        if (result.data["Response"] != "ok") {
                            vm.setSetTimeOutAlertMsg(result.data["Response"]);
                            vm.toggleSetTimeOutAlertModal();
                            return;
                        }
                        vm.setApiMap(result.data["api_map"]);
                        let obj = {};
                        obj.account = vm.user.account;
                        obj.status = true;
                        obj.username = vm.user.name;
                        obj.accesslist = vm.user.access_list;
                        obj.customerId = vm.user.customerId;

                        vm.changeLoginData(obj);
                        vm.changePageAccess(obj.accesslist);
                        vm.changeIsLoginMoment(true);

                        var access = obj.accesslist;
                        const rootaccesslistlength = Object.keys(access).length;
                        console.log(rootaccesslistlength);
                        let mainfunctruenum = 0;
                        let trueList = [];
                        Object.entries(access).forEach((element) => {
                            console.log(element);
                            if (element[1]["authority"]) {
                                mainfunctruenum += 1;
                                if (element[1].hasOwnProperty("children")) {
                                    Object.keys(element[1].children).map(
                                        (item) => {
                                            if (
                                                element[1].children[item]
                                                    .authority
                                            ) {
                                                trueList.push(
                                                    element[0] + "/" + item
                                                );
                                            }
                                        }
                                    );
                                } else {
                                    trueList.push(element[0]);
                                }
                            }
                        });
                        console.log(mainfunctruenum);
                        //為Root登入
                        if (mainfunctruenum == rootaccesslistlength) {
                            vm.changeIsRoot(true);
                        }
                        trueList.push("home");
                        console.log(trueList);
                        console.log(childRouter[0].children);
                        childRouter[0].children = childRouter[0].children.filter(
                            (item) => trueList.includes(item.path)
                        );
                        console.log(childRouter[0].children);
                        vm.$router.addRoutes(childRouter);
                        console.log(vm.$router);
                        vm.toggleAlertModal(false);
                        setTimeout(function () {
                            vm.$router.push("/" + vm.SYSTEM);
                        }, 900);
                    });
                })
                .catch((response) => {
                    console.log(response);
                    vm.toggleAlertModal(false);
                    vm.setSetTimeOutAlertMsg(response);
                    vm.toggleSetTimeOutAlertModal();
                });
        },

        _login() {
            return new Promise((resolve, reject) => {
                const vm = this;
                let params = {
                    url:
                        "/api/" + vm.SYSTEM + "/1.0/my/CommonUse/Interval/user",
                    urlparams: {
                        attr: "user_id",
                        valueStart: vm.user.account,
                        valueEnd: vm.user.account,
                    },
                };
                vm.axiosGetAction(params)
                    .then(() => {
                        var result = vm.axiosResult;
                        console.log(result);
                        if (result.status != 200) {
                            reject(result.data);
                        }
                        if (result.data["Response"] != "ok") {
                            reject(result.data["Response"]);
                        }
                        if (result.data["QueryTableData"].length == 0) {
                            reject("帳號或密碼錯誤");
                        }
                        if (
                            result.data["QueryTableData"][0]["delete_enable"] ==
                            "Y"
                        ) {
                            reject("此帳號已註銷");
                        }
                        if (
                            result.data["QueryTableData"][0]["pwd"] !=
                            vm.user.password
                        ) {
                            reject("帳號或密碼錯誤");
                        }
                        vm.user.name = result.data["QueryTableData"][0]["name"];
                        vm.user.access_list = JSON.parse(
                            result.data["QueryTableData"][0]["access_list"]
                        )["web"];
                        vm.user.customerId = result.data["QueryTableData"][0]["cus_id"];
                        console.log(vm.user);
                        resolve("ok");
                    })
                    .catch((response) => {
                        console.log(response);
                        reject(response);
                    });
            });
        },

        _showHidePwd(which) {
            let vm = this;
            if (which) {
                vm.pwd_show = !vm.pwd_show;
                if (vm.pwd_show) {
                    vm.pwd_type = "text";
                } else {
                    vm.pwd_type = "password";
                }
            } else {
                vm.confirmpwd_show = !vm.confirmpwd_show;
                if (vm.confirmpwd_show) {
                    vm.confirmpwd_type = "text";
                } else {
                    vm.confirmpwd_type = "password";
                }
            }
        },
    },
};
</script>

