<script>
import { mapGetters, mapActions } from "vuex";
import Temp from "../Temp.vue";
export default {
    components: { Temp },
    name: "account",
    data() {
        let vm = this;
        return {
            action: "cud",
            action_config: {
                基本資料設定: "cud",
                基本資料查詢: "query",
                cud: "基本資料設定",
                query: "基本資料查詢",
            },
            form: {
