                },
            },
            query_select: {
                selected: "",
                options: [],
            },
            cus_fields: [
                {
                    key: "id",
                    label: "客戶編號",
                    thClass: "publicTH",
                },
                {
                    key: "cus_name",
                    label: "客戶名稱",
                    thClass: "publicTH",
                },
                {
                    key: "contact_person",
                    label: "聯絡人",
                    thClass: "publicTH",
                },
                {
                    key: "telphone",
                    label: "聯絡電話",
                    thClass: "publicTH",
                },
                {
                    key: "detail",
                    label: "詳細資料",
                    thClass: "publicTH",
                },
                {
                    key: "del",
                    label: "刪除",
                    thClass: "publicTH",
                },
            ],
            cus_items: [],
            email_fields: [
                {
                    key: "choice",
                    label: "",
                    thClass: "publicTH",
                },
                {
                    key: "email",
                    label: "機台異常通知信箱",
                    thClass: "publicTH",
                },
                {
                    key: "0",
                    label: "無",
                    thClass: "publicTH",
                },
                {
                    key: "1",
                    label: "輕",
                    thClass: "publicTH",
                },
                {
                    key: "2",
                    label: "重",
                    thClass: "publicTH",
                },
            ],
            email_items: [],
            id_existed: "",
            pwd_show: false,
            pwd_type: "password",
            confirmpwd_show: false,
            confirmpwd_type: "password",
            is_add_update: "add",
            choiceRow: "no choice",
        };
    },
    computed: {
        ...mapGetters({
            loginData: "getlogin/get_loginData",
            axiosResult: "commonaxios/get_axiosResult",
            // system: "apidetail/get_system",
            DEFAULT_commonModalConfig: "usemodal/get_DEFAULT_commonModalConfig",
            clickChild: "featurelistbar/get_clickChild",
        }),
        accountblock: function () {
            if (this.action == "cud") {
                return {
                    "account-block": true,
                };
            } else {
                return {
                    "account-block": false,
                };
            }
        },
    },
    watch: {
        "clickChild.child_name": {
            handler(value) {
                let vm = this;
                console.log("@@@@@@@");
                console.log(vm.clickChild);
                console.log(value);
                vm._reset([]);
                vm.action = vm.action_config[value.split("_")[1]];
                if (vm.action == "query") {
                    vm._queryCusID();
                }
            },
        },
    },
    created() {
        // this._checkApiDetailExist();
        // this.apipath_prefix += this.$route.params.SYSTEM;
        console.log(this.$router);
        console.log(this.$router.currentRoute.matched);
        console.log(this.endpoint);
        console.log(this.$route.params.SYSTEM);
        console.log(this.clickChild);
        if (
            this.action_config[this.clickChild.child_name.split("_")[1]] ==
            "query"
        ) {
            this.action = "query";
            this._queryCusID();
        }
    },
    mounted() {
        // var VueScrollTo = require("vue-scrollto");
        // var options = {
        //     container: "#account",
        //     easing: "ease-in",
        //     offset: -60,
        //     force: true,
        //     cancelable: true,
        //     onStart: function (element) {
        //         // scrolling started
        //     },
        //     onDone: function (element) {
        //         // scrolling is done
        //     },
        //     onCancel: function () {
        //         // scrolling has been interrupted
        //     },
        //     x: false,
        //     y: true,
        // };
        // var cancelScroll = VueScrollTo.scrollTo("#account", options);
        // // or alternatively inside your components you can use
        // // cancelScroll = this.$scrollTo(element, duration, options);
        // // to cancel scrolling you can call the returned function
        // cancelScroll();
        // // this.$scrollTo("#account");
    },
    methods: {
        ...mapActions({
            axiosAction: "commonaxios/axiosAction",
            axiosGetAction: "commonaxios/axiosGetAction",
            axiosPostAction: "commonaxios/axiosPostAction",
            axiosDeleteAction: "commonaxios/axiosDeleteAction",
            setSetTimeOutAlertMsg: "alertmodal/set_setTimeOutAlertMsg",
            toggleSetTimeOutAlertModal:
                "alertmodal/toggle_settimeout_alertModal",
            toggleAlertModal: "alertmodal/toggle_alertModal",
            toggleCommonModal: "usemodal/toggle_commonModal",
            setCommonModalConfig: "usemodal/set_commonModalConfig",
        }),

        _rowClass(item, type) {
            if (!item || type !== "row") return;
        },

        _onSubmit(event) {
            event.preventDefault();
            event.stopPropagation();
            let vm = this;
            console.log(vm.form);
            let isok = true;
            Object.entries(vm.form).map((ele) => {
                if (
                    ele[0] != "remarks" &&
                    ele[0] != "tax_id" &&
                    ele[0] != "id" &&
                    ele[0] != "old_user_id" &&
                    ele[0] != "mail" &&
                    ele[1] == ""
                ) {
                    isok = false;
                }
            });
            if (!isok) {
                vm.setSetTimeOutAlertMsg("資料尚未輸入完整");
                vm.toggleSetTimeOutAlertModal();
                return;
            }
            if (vm.form.pwd != vm.form.confirmpwd) {
                vm.setSetTimeOutAlertMsg("確認密碼與密碼不相同");
                vm.toggleSetTimeOutAlertModal();
                vm.form.pwd = "";
                vm.form.confirmpwd = "";
                return;
            }

            if (vm.is_add_update == "add") {
                const addUser = async () => {
                    await vm._checkTaxNumber(); //檢查統編
                    await vm._checkEmailCheckbox(); //檢查email格式
                    await vm._checkUserID(); //檢查userID是否存在
                    await vm._addCustomer(); //新增客戶資料
                };
                addUser()
                    .then(() => {
                        let vm = this;
                        vm.toggleAlertModal(true);
                        console.log("go add");
                        console.log(vm.form);
                        let params = {};
                        params["url"] =
                            "/api/" +
                            vm.$route.params.SYSTEM +
                            "/1.0/my/user/reg_User";
                        params["postdata"] = {
                            user_id: vm.form.user_id,
                            cus_id: vm.form.cus_id,
                            pwd: vm.form.pwd,
                            name: vm.form.cus_name,
                            email: "",
                            access_list: vm.access_list,
                            // delete_enable: [null],
                            delete_at: [null],
                            creator: vm.loginData.account,
                            modifier: vm.loginData.account,
                        };
                        console.log(params);

                        vm.axiosPostAction(params)
                            .then(() => {
                                var result = vm.axiosResult;
                                console.log(result);
                                if (result.status != 200) {
                                    vm.setSetTimeOutAlertMsg(result.data);
                                    return;
                                }
                                if (result.data["Response"] == "ok") {
                                    vm.setSetTimeOutAlertMsg("新增成功");
                                    vm._reset([]);
                                } else {
                                    vm.setSetTimeOutAlertMsg(
                                        result.data["Response"]
                                    );
                                }
                            })
                            .catch((response) => {
                                console.log(response);
                                vm.setSetTimeOutAlertMsg(response);
                            })
                            .finally(() => {
                                console.log("done");
                                vm.toggleAlertModal(false);
                                vm.toggleSetTimeOutAlertModal();
                            });
                    })
                    .catch((response) => {
                        console.log(response);
                        vm.setSetTimeOutAlertMsg(response);
                        vm.toggleSetTimeOutAlertModal();
                    });
            } else {
                const updateUser = async () => {
                    await vm._checkTaxNumber(); //檢查統編
                    await vm._checkEmailCheckbox(); //檢查email格式
                    if (vm.form.old_user_id != vm.form.user_id) {
                        await vm._checkUserID(); //檢查userID是否存在
                    }
                    await vm._updateCustomer(); //修改客戶
                };
                updateUser()
                    .then(() => {
                        let params = {};
                        params["methods"] = "PATCH";
                        params["whichFunction"] = "userUpdate";
                        params["postdata"] = {
                            old_user_id: vm.form.old_user_id,
                            new_user_id: vm.form.user_id,
                            cus_id: vm.form.id,
                            pwd: vm.form.pwd,
                            name: vm.form.cus_name,
                            email: "",
                            access_list: vm.form.access_list,
                            modifier: vm.loginData.account,
                        };
                        console.log(params);
                        vm.axiosAction(params)
                            .then(() => {
                                var result = vm.axiosResult;
                                console.log(result);
                                if (result["Response"] == "ok") {
                                    vm.setSetTimeOutAlertMsg("修改成功");
                                } else {
                                    vm.setSetTimeOutAlertMsg(
                                        result["Response"]
                                    );
                                }
                            })
                            .catch((response) => {
                                console.log(response);
                                vm.setSetTimeOutAlertMsg(response);
                            })
                            .finally(() => {
                                vm.toggleSetTimeOutAlertModal();
                                vm._reset(["query_select"]);
                                vm.action = "query";
                                vm._queryCustomer();
                            });
                    })
                    .catch((response) => {
                        console.log(response);
                        vm.setSetTimeOutAlertMsg(response);
                        vm.toggleSetTimeOutAlertModal();
                    });
            }
        },

        _showHidePwd(which) {
            let vm = this;
            if (which) {
                vm.pwd_show = !vm.pwd_show;
                if (vm.pwd_show) {
                    vm.pwd_type = "text";
                } else {
                    vm.pwd_type = "password";
                }
            } else {
                vm.confirmpwd_show = !vm.confirmpwd_show;
                if (vm.confirmpwd_show) {
                    vm.confirmpwd_type = "text";
                } else {
                    vm.confirmpwd_type = "password";
                }
            }
        },

        _checkTaxNumber() {
            return new Promise((resolve, reject) => {
                const vm = this;
                if (vm.form.tax_id == "") {
                    resolve("ok");
                }
                var validateOperator = [1, 2, 1, 2, 1, 2, 4, 1];
                if (vm.form.tax_id.length !== 8) {
                    reject("統編格式錯誤，要有8個數字");
                }
                let sum = 0;
                const calculate = function (product) {
                    // 個位數 + 十位數
                    var ones = product % 10,
                        tens = (product - ones) / 10;
                    return ones + tens;
                };
                for (var i = 0; i < validateOperator.length; i++) {
                    sum += calculate(vm.form.tax_id[i] * validateOperator[i]);
                }

                if (
                    sum % 10 == 0 ||
                    (vm.form.tax_id[6] == "7" && (sum + 1) % 10 == 0)
                ) {
                    resolve("ok");
                } else {
                    reject("統一編號錯誤");
                }
            });
        },

        _checkEmailCheckbox() {
            return new Promise((resolve, reject) => {
                const vm = this;
                const emailRule = /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/;
                console.log(vm.email_items);
                if (vm.email_items.length == 0) {
                    vm.form.mail = JSON.stringify({});
                    resolve("ok");
                } else {
                    let isok = true;
                    let mailobj = new Object();
                    for (let i = 0; i < vm.email_items.length; i++) {
                        console.log(i);
                        const element = vm.email_items[i];
                        let checkboxnum = 0;
                        console.log(element);
                        console.log(element.email);
                        if (element.email != "") {
                            if (element.email.search(emailRule) != -1) {
                                console.log("emailok");
                                if (!mailobj.hasOwnProperty(element.email)) {
                                    mailobj[element.email] = new Array();
                                    vm.email_items[i].warnClass.email = false;
                                } else {
                                    vm.email_items[i].warnClass.email = true;
                                    isok = false;
                                }
                            } else {
                                console.log("emailfail");
                                vm.email_items[i].warnClass.email = true;
                                isok = false;
                            }

                            if (isok) {
                                console.log(element[0]);
                                console.log(element[1]);
                                console.log(element[2]);
                                for (let i = 0; i < 3; i++) {
                                    if (element[i]) {
                                        checkboxnum += 1;
                                        mailobj[element.email].push(i);
                                    }
                                }
                            }
                            console.log(checkboxnum);
                            if (checkboxnum == 0) {
                                vm.email_items[i].warnClass.checkbox = true;
                                isok = false;
                            } else {
                                vm.email_items[i].warnClass.checkbox = false;
                            }
                        } else {
                            vm.email_items[i].warnClass.email = true;
                            vm.email_items[i].warnClass.checkbox = true;
                            isok = false;
                        }
                    }
                    console.log(isok);
                    console.log(mailobj);
                    if (isok) {
                        resolve("ok");
                        vm.form.mail = JSON.stringify(mailobj);
                    } else {
                        reject("信箱或異常設定未輸入完整");
                    }
                }
            });
        },

        _checkUserID() {
            return new Promise((resolve, reject) => {
                let vm = this;
