                params["url"] =
                    "/api/" +
                    vm.$route.params.SYSTEM +
                    "/1.0/my/CommonUse/Interval/user";
                params["urlparams"] = {
                    attr: "user_id",
                    valueStart: vm.form.user_id,
                    valueEnd: vm.form.user_id,
                };
                console.log(params);
                vm.axiosGetAction(params)
                    .then(() => {
                        var result = vm.axiosResult;
                        console.log(result);
                        if (result.status != 200) {
                            reject(result.data);
                        }
                        if (result.data["Response"] == "ok") {
                            if (result.data["QueryTableData"].length > 0) {
                                vm.id_existed = true;
                                reject("此帳號已被註冊使用");
                            } else {
                                vm.id_existed = false;
                                resolve("ok");
                            }
                        } else {
                            reject(result.data["Response"]);
                        }
                    })
                    .catch((response) => {
                        console.log(response);
                        reject(response);
                    });
            });
        },

