        _queryCusID() {
            let vm = this;
            let params = {};
            vm._reset(["action"]);
            //檢查帳號是否存在
            params["url"] =
                "/api/" +
                vm.$route.params.SYSTEM +
                "/1.0/my/CommonUse/TableData";
            params["urlparams"] = {
                table: "customer",
            };
            console.log(params);
            let showmsg = true;
            vm.axiosGetAction(params)
                .then(() => {
                    var result = vm.axiosResult;
                    console.log(result);
                    if (result.status != 200) {
                        vm.setSetTimeOutAlertMsg(result.data);
                        return;
                    }
                    let options = [];
                    if (result.data["Response"] == "ok") {
                        if (result.data["QueryTableData"].length > 0) {
                            result.data["QueryTableData"].map((ele) => {
                                if (ele.delete_enable == "N") {
                                    options.push({
                                        text: ele.id + " " + ele.cus_name,
                                        value: ele.id,
                                    });
                                    showmsg = false;
                                }
                            });
                        }
                    } else {
                        vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    }
                    console.log(options);
                    vm.query_select.options = options;
                    if (vm.query_select.options.length == 0) {
                        vm.setSetTimeOutAlertMsg("查無資料");
                    }
                })
                .catch((response) => {
                    console.log(response);
                    vm.setSetTimeOutAlertMsg(response);
                })
                .finally(() => {
                    if (showmsg) {
                        vm.toggleSetTimeOutAlertModal();
                    }
                });
        },

        _queryCustomer() {
            let vm = this;
            let params = {};
            vm._reset(["query_select", "action"]);
            params["url"] =
                "/api/" +
                vm.$route.params.SYSTEM +
                "/1.0/my/CommonUse/Interval/customer";
            params["urlparams"] = {
                uid: "@sapido@PaaS",
                attr: "id",
                valueStart: vm.query_select.selected,
                valueEnd: vm.query_select.selected,
            };
            console.log(params);
            let showmsg = true;
            vm.axiosGetAction(params)
                .then(() => {
                    var result = vm.axiosResult;
                    console.log(result);
                    if (result.status != 200) {
                        vm.setSetTimeOutAlertMsg(result.data);
                        return;
                    }
                    if (result.data["Response"] == "ok") {
                        if (result.data["QueryTableData"].length > 0) {
                            result.data["QueryTableData"].map((ele) => {
                                if (ele.delete_enable == "N") {
                                    vm.cus_items.push({
                                        id: ele.id,
                                        cus_name: ele.cus_name,
                                        tax_id: ele.tax_id,
                                        owner: ele.owner,
                                        contact_person: ele.contact_person,
                                        telphone: ele.telphone,
                                        address: ele.address,
                                        remarks: ele.remarks,
                                        mail: JSON.parse(ele.mail),
                                    });
                                    showmsg = false;
                                }
                            });
                        }
                        if (vm.cus_items.length == 0) {
                            vm.setSetTimeOutAlertMsg("查無資料");
                        }
                    } else {
                        vm.setSetTimeOutAlertMsg(result.data["Response"]);
                    }
                })
                .catch((response) => {
                    console.log(response);
                    vm.setSetTimeOutAlertMsg(response);
                })
                .finally(() => {
                    if (showmsg) {
                        vm.toggleSetTimeOutAlertModal();
                    }
                });
        },

        _checkDetail(item) {
            console.log(item);
            let vm = this;
            let params = {};
            //抓取對應帳號
            params["url"] =
                "/api/" +
                vm.$route.params.SYSTEM +
                "/1.0/my/CommonUse/Interval/user";
            params["urlparams"] = {
                uid: "@sapido@PaaS",
                attr: "cus_id",
                valueStart: item.id,
                valueEnd: item.id,
            };
            console.log(params);
            vm.axiosGetAction(params)
                .then(() => {
                    var result = vm.axiosResult;
                    console.log(result);
                    if (result.status != 200) {
                        vm.setSetTimeOutAlertMsg(result.data);
                        vm.toggleSetTimeOutAlertModal();
                        return;
                    }
                    if (
                        result.data["Response"] == "ok" &&
                        result.data["QueryTableData"].length > 0
                    ) {
                        let mailobj = [];
                        if (Object.keys(item.mail).length > 0) {
                            Object.entries(item.mail).map((ele) => {
                                mailobj.push({
                                    email: ele[0],
                                    0: ele[1].indexOf(0) != -1,
                                    1: ele[1].indexOf(1) != -1,
                                    2: ele[1].indexOf(2) != -1,
                                    warnClass: {
                                        email: false,
                                        checkbox: false,
                                    },
                                });
                            });
                        }
                        console.log(mailobj);
                        console.log(vm.email_items);
                        vm.email_items = mailobj;
                        vm.form = {
                            id: item.id,
                            cus_id: item.id,
                            tax_id: item.tax_id,
                            old_user_id:
                                result.data["QueryTableData"][0]["user_id"],
                            user_id:
                                result.data["QueryTableData"][0]["user_id"],
                            pwd: result.data["QueryTableData"][0]["pwd"],
                            confirmpwd: result.data["QueryTableData"][0]["pwd"],
                            cus_name: item.cus_name,
                            owner: item.owner,
                            contact_person: item.contact_person,
                            telphone: item.telphone,
                            address: item.address,
                            remarks: item.remarks,
                            access_list: JSON.parse(
                                result.data["QueryTableData"][0]["access_list"]
                            ),
                        };
                    }
                })
                .catch((response) => {
                    console.log(response);
                })
                .finally(() => {
                    vm.action = "cud";
                    vm.is_add_update = "update";
                });
        },

        _deleteCustomer(cus_id) {
            console.log(cus_id);
            let vm = this;
            this.$bvModal
                .msgBoxConfirm("確定刪除", {
                    size: "sm",
                    okVariant: "primary",
                    okTitle: "確定",
                    cancelTitle: "取消",
                    bodyClass: "delconfirm",
                    hideHeader: true,
                    hideHeaderClose: true,
                    noCloseOnBackdrop: true,
                    noCloseOnEsc: true,
                })
                .then((value) => {
                    console.log(value);
                    if (value) {
                        let params = {};
                        //抓取對應帳號
                        params["url"] =
                            "/api/" +
                            vm.$route.params.SYSTEM +
                            "/1.0/Customized/Customer/Macro";
                        params["postdata"] = {
                            cus_id: cus_id,
                        };
                        console.log(params);
                        vm.axiosDeleteAction(params)
                            .then(() => {
                                var result = vm.axiosResult;
                                console.log(result);
                                if (result.status != 200) {
                                    vm.setSetTimeOutAlertMsg(result.data);
                                    return;
                                }
                                if (result.data["Response"] == "ok") {
                                    vm.setSetTimeOutAlertMsg("刪除成功");
                                } else {
                                    vm.setSetTimeOutAlertMsg(
                                        result.data["Response"]
                                    );
                                }
                            })
                            .catch((response) => {
                                console.log(response);
                                vm.setSetTimeOutAlertMsg(response);
                            })
                            .finally(() => {
                                vm.toggleSetTimeOutAlertModal();
                                setTimeout(function () {
                                    vm._queryCusID();
                                }, 1000);
                            });
                    }
                })
                .catch((err) => {
                    // An error occurred
                });
        },

        _addEmailEle() {
            let vm = this;
            vm.email_items.push({
                email: "",
                0: false,
                1: false,
                2: false,
                warnClass: {
                    email: false,
                    checkbox: false,
                },
            });
            this.$nextTick(() => {
                this.$refs.emailTable.selectRow(vm.choiceRow);
            });
        },

        _removeEmailEle() {
            let vm = this;
            if (vm.choiceRow != "no choice") {
                vm.email_items.splice(vm.choiceRow, 1);
                vm.choiceRow = "no choice";
            }
        },

        _choiceEmailEle(item, index) {
            this.choiceRow = index;
        },

        //data Reset
        _reset(keep) {
            var def = this.$options.data();
            for (var i = 0; i < keep.length; i++) {
                def[keep[i]] = this[keep[i]];
            }
            Object.assign(this.$data, def);
            //https://codepen.io/karimcossutti/pen/ObXyKq
        },
    },
};
</script>

